import React, { useState, useContext } from 'react'
import { Link, useNavigate } from 'react-router-dom';
import Navbar from 'react-bootstrap/Navbar';
import { Container, Nav } from 'react-bootstrap';
import { UserContext } from '../../../App';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import AllPropertiesCards from '../AllPropertiesCards';
import AllProperty from './AllProperties';
import HomeIcon from '@mui/icons-material/Home';

function CustomTabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

CustomTabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const navLinkStyle = {
   textDecoration: 'none',
   color: '#000',
   padding: '8px 16px',
   margin: '0 8px',
   borderRadius: '4px',
   border: '1px solid #ddd',
   backgroundColor: '#fff',
   transition: 'all 0.3s ease',
   display: 'inline-block'
};

const navLinkHoverStyle = {
   backgroundColor: '#f8f9fa',
   borderColor: '#adb5bd',
   color: '#000'
};

const RenterHome = () => {
  const { userData, userLoggedIn } = useContext(UserContext);
  const navigate = useNavigate();
  const [value, setValue] = useState(0);
  const [hoveredLink, setHoveredLink] = useState('');

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  if (!userLoggedIn || !userData) {
    navigate('/login');
    return null;
  }

  const handleLogOut = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    navigate('/');
  }

  return (
    <div>
      <Navbar expand="lg" className="bg-body-tertiary">
        <Container fluid>
          <Navbar.Brand>
            <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
              <HomeIcon style={{ fontSize: '2rem', color: 'black' }} />
              <h2 style={{ margin: 0 }}>HouseHunt</h2>
            </div>
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarScroll" />
          <Navbar.Collapse id="navbarScroll">
            <Nav
              className="me-auto my-2 my-lg-0"
              style={{ maxHeight: '100px' }}
              navbarScroll
            >
            </Nav>
            <Nav style={{ display: 'flex', gap: '10px', alignItems: 'center' }}>
              <h5 className='mx-3' style={{ margin: 0 }}>Hi {userData.name}</h5>
              <Link 
                onClick={handleLogOut} 
                to={'/'}
                style={{
                  ...navLinkStyle,
                  ...(hoveredLink === 'logout' ? navLinkHoverStyle : {}),
                  backgroundColor: '#dc3545',
                  borderColor: '#dc3545',
                  color: '#fff'
                }}
                onMouseEnter={() => setHoveredLink('logout')}
                onMouseLeave={() => setHoveredLink('')}
              >
                Log Out
              </Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Box sx={{ width: '100%' }}>
        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
          <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
            <Tab label="All Properties" {...a11yProps(0)} />
            <Tab label="Booking History" {...a11yProps(1)} />
          </Tabs>
        </Box>
        <CustomTabPanel value={value} index={0}>
          <Container>
            <AllPropertiesCards loggedIn={userLoggedIn} />
          </Container>
        </CustomTabPanel>
        <CustomTabPanel value={value} index={1}>
          <AllProperty/>
        </CustomTabPanel>
      </Box>
    </div>
  )
}

export default RenterHome

