import React, { useState, useContext } from 'react'
import { Link, useNavigate } from 'react-router-dom';
import Navbar from 'react-bootstrap/Navbar';
import { Container, Nav } from 'react-bootstrap';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import HomeIcon from '@mui/icons-material/Home';
import Typography from '@mui/material/Typography';
import axios from 'axios';
import { message } from 'antd';
import { UserContext } from '../../../src/App';

const navLinkStyle = {
   textDecoration: 'none',
   color: '#000',
   padding: '8px 16px',
   margin: '0 8px',
   borderRadius: '4px',
   border: '1px solid #ddd',
   backgroundColor: '#fff',
   transition: 'all 0.3s ease',
   display: 'inline-block'
};

const navLinkHoverStyle = {
   backgroundColor: '#f8f9fa',
   borderColor: '#adb5bd',
   color: '#000'
};

const Login = () => {
  const navigate = useNavigate()
  const [data, setData] = useState({
    email: "",
    password: ""
  })
  const { setUserData, setUserLoggedIn } = useContext(UserContext);
  const [hoveredLink, setHoveredLink] = useState('');

  const handleChange = (e) => {
    const { name, value } = e.target;
    setData({ ...data, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!data?.email || !data?.password) {
      message.error("Please fill all fields");
      return;
    }

    try {
      const response = await axios.post('http://localhost:8000/api/user/login', data);
      
      if (response.data.success) {
        message.success(response.data.message);
        localStorage.setItem("token", response.data.token);
        localStorage.setItem("user", JSON.stringify(response.data.user));
        
        const user = response.data.user;
        setUserData(user);
        setUserLoggedIn(true);

        switch (user.type) {
          case "Admin":
            navigate("/adminhome");
            break;
          case "Renter":
            navigate("/renterhome");
            break;
          case "Owner":
            if (user.granted === 'ungranted') {
              message.error('Your account is not yet confirmed by the admin');
              localStorage.clear();
              setUserData(null);
              setUserLoggedIn(false);
            } else {
              navigate("/ownerhome");
            }
            break;
          default:
            message.error("Invalid user type");
            localStorage.clear();
            setUserData(null);
            setUserLoggedIn(false);
            break;
        }
      } else {
        message.error(response.data.message || "Login failed");
        localStorage.clear();
        setUserData(null);
        setUserLoggedIn(false);
      }
    } catch (error) {
      console.error('Login error:', error);
      if (error.response) {
        message.error(error.response.data.message || "Invalid credentials");
      } else if (error.request) {
        message.error("Server not responding. Please try again later.");
      } else {
        message.error("Login failed. Please try again.");
      }
      localStorage.clear();
      setUserData(null);
      setUserLoggedIn(false);
    }
  };

  return (
    <>
      <Navbar expand="lg" className="bg-body-tertiary">
        <Container fluid>
          <Navbar.Brand>
            <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
              <HomeIcon style={{ fontSize: '2rem', color: 'black' }} />
              <h2 style={{ margin: 0 }}>HouseHunt</h2>
            </div>
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarScroll" />
          <Navbar.Collapse id="navbarScroll">
            <Nav
              className="me-auto my-2 my-lg-0"
              style={{ maxHeight: '100px' }}
              navbarScroll
            >
            </Nav>
            <Nav style={{ display: 'flex', gap: '10px', alignItems: 'center' }}>
              <Link 
                to={'/'}
                style={{
                  ...navLinkStyle,
                  ...(hoveredLink === 'home' ? navLinkHoverStyle : {})
                }}
                onMouseEnter={() => setHoveredLink('home')}
                onMouseLeave={() => setHoveredLink('')}
              >
                Home
              </Link>
              <Link 
                to={'/login'}
                style={{
                  ...navLinkStyle,
                  ...(hoveredLink === 'login' ? navLinkHoverStyle : {})
                }}
                onMouseEnter={() => setHoveredLink('login')}
                onMouseLeave={() => setHoveredLink('')}
              >
                Login
              </Link>
              <Link 
                to={'/register'}
                style={{
                  ...navLinkStyle,
                  ...(hoveredLink === 'register' ? navLinkHoverStyle : {})
                }}
                onMouseEnter={() => setHoveredLink('register')}
                onMouseLeave={() => setHoveredLink('')}
              >
                Register
              </Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Container component="main" >
        <Box
          sx={{
            marginTop: 8,
            marginBottom: 4,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Avatar sx={{ bgcolor: 'secondary.main' }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign In
          </Typography>
          <Box component="form" onSubmit={handleSubmit} noValidate>
            <TextField
              margin="normal"
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              value={data.email}
              onChange={handleChange}
              autoComplete="email"
              autoFocus
            />
            <TextField
              margin="normal"
              fullWidth
              name="password"
              value={data.password}
              onChange={handleChange}
              label="Password"
              type="password"
              id="password"
              autoComplete="current-password"
            />
            <Box mt={2}>
              <Button
                type="submit"
                variant="contained"
                style={{ width: '200px' }}
              >
                Sign In
              </Button>
            </Box>
            <Grid container>
              <Grid item>Forgot password?
                <Link style={{ color: "red" }} to={'/forgotpassword'} variant="body2">
                  {" Click here"}
                </Link>
              </Grid>
              <Grid item>Don't have an account?
                <Link style={{ color: "blue" }} to={'/register'} variant="body2">
                  {" Sign Up"}
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>
      </Container>
    </>
  )
}

export default Login
